import React, { useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import MainHeader from "../../components/Users/MainHeader";
import Alertpopup from "../../components/Alertpopup";
import Alertpopupsuccess from "../../components/Alertpopupsuccess";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Stepblock,
  Titletext,
  Iconblock,
  Dealcontent,
  SectionWrapper,
  Overlay,
  PopupBox,
  CloseBtn,
  Input,
  SubmitBtn,
  Wrapper,
  StyledIcon,
} from "../../components/Styles/RegisterStyles";
import PaymentPopup from "../../components/Users/PaymentPopup"; // Adjust path

import { useParams, useNavigate } from "react-router-dom";
import moment from "moment";
import { Building, Clock, Video, Earth } from "lucide-react";
import { Calendar, momentLocalizer, Navigate } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import { format, toZonedTime } from "date-fns-tz";
import ModuleSideNav from "../../components/Users/ModuleSideNav";
import axios from "axios";
export default function ModuleOne() {
  const navigate = useNavigate();
  const [showPopup, setShowPopup] = useState(false);
  const [showPopupPay, setShowPopupPay] = useState(false);
  const [formData, setFormData] = useState({ name: "", email: "" });
  const localizer = momentLocalizer(moment);
  const [selectedSlots, setSelectedSlots] = useState([]);
  const [ClientIP, setClientIP] = useState("");
  const [message, setMessage] = useState("");
  const [errorMsg, seterrorMsg] = useState("");
  const [successMsg, setsuccessMsg] = useState("");
  const [selectedTime, setSelectedTime] = useState(null);
  const [SelectedTimehours, setSelectedTimehours] = useState("");
  const [disabledButton, setdisabledButton] = useState(true);
  const [currentTime, setCurrentTime] = useState("");
  const [moduledata, setmoduledata] = useState("");
  const [loader, setloader] = useState("");
  const [checkModulesub, setcheckModulesub] = useState("");
  const { id } = useParams();
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [Descriptionvalue, setDescriptionvalue] = useState("");
  const apiURL = "https://blueprintcatalyst.com/api/user/";
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  useEffect(() => {
    // Get supported time zones dynamically
    if (Intl.supportedValuesOf) {
      const zones = Intl.supportedValuesOf("timeZone");
      setTimeZones(zones);
    }
  }, []);
  useEffect(() => {
    if (id !== "undefined") {
      checkmodulesubscription();
    }
  }, [id]);
  const checkmodulesubscription = async () => {
    let formdata = {
      id: id,
    };
    try {
      const res = await axios.post(
        apiURL + "checkmodulesubscription",
        formdata,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      if (res.data.results.length > 0) {
        setcheckModulesub("1");
      }
    } catch (err) {}
  };
  useEffect(() => {
    const getmoduledata = async () => {
      let formdata = {
        id: id,
      };
      try {
        const res = await axios.post(apiURL + "selectModule", formdata, {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        });
        if (res.data.results.length > 0) {
          checkmodulesubscription();
          setmoduledata(res.data.results[0]);
        } else {
          navigate("/dashboard");
        }
      } catch (err) {}
    };
    getmoduledata();
  }, [id]);
  useEffect(() => {
    document.title = "Module Page";
  }, []);
  const handleSelectSlot = ({ start, end }) => {
    if (checkModulesub === "") {
      setShowPopupPay(true);
    } else {
      const today = new Date();
      today.setHours(0, 0, 0, 0); // Midnight today
      const slotDate = new Date(start);
      slotDate.setHours(0, 0, 0, 0); // Compare by date only

      // Check if selected date is in the past
      if (slotDate < today) {
        seterrorMsg("You cannot select a past date.");
        return;
      }

      // Check if the date is already selected
      const isDateAlreadySelected = selectedSlots.some(
        (slot) =>
          new Date(slot.start).setHours(0, 0, 0, 0) === slotDate.getTime()
      );

      if (isDateAlreadySelected) {
        // Unselect the date by removing it from selectedSlots
        const updatedSlots = selectedSlots.filter(
          (slot) =>
            new Date(slot.start).setHours(0, 0, 0, 0) !== slotDate.getTime()
        );
        setSelectedSlots(updatedSlots);
        seterrorMsg("");
        setdisabledButton(updatedSlots.length === 0); // Disable button if no slots
        return;
      }

      // Check if user has already selected 3 slots
      if (selectedSlots.length >= 3) {
        seterrorMsg("You can only select up to 3 meetings.");
        return;
      }

      // Add new slot
      const newSlot = { start, end };
      const updatedSlots = [...selectedSlots, newSlot];
      setSelectedSlots(updatedSlots);
      seterrorMsg("");
      setdisabledButton(false);
    }
  };

  useEffect(() => {
    const getIP = async () => {
      try {
        const res = await fetch("https://api.ipify.org?format=json");
        const data = await res.json();
        setClientIP(data.ip); // Save this to state
      } catch (error) {
        console.error("Failed to fetch IP", error);
      }
    };

    getIP();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    let formdata = {
      name: e.target.name.value,
      email: e.target.email.value,
      timeset: SelectedTimehours,
      module_id: id,
      ip_address: ClientIP,
      selectedZone: selectedZone,
      selectedSlots: selectedSlots,
      description: Descriptionvalue,
    };

    setloader(true);
    try {
      const res = await axios.post(apiURL + "registerforZoom", formdata, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      setloader(false);
      var checkdata = res.data;
      if (checkdata.status === "2") {
        setsuccessMsg("");
        seterrorMsg(checkdata.message);
        return;
      }
      if (checkdata.status === "1") {
        seterrorMsg("");
        setsuccessMsg(checkdata.message);
        setShowPopup(false);
        setTimeout(() => {
          window.location.reload();
        }, 3000);
        return;
      }
    } catch (err) {}
    console.log("Form submitted", formdata);
    //setShowPopup(false);
  };
  const [timeZones, setTimeZones] = useState([]);
  const [selectedZone, setSelectedZone] = useState(
    Intl.DateTimeFormat().resolvedOptions().timeZone
  );
  useEffect(() => {
    // Get supported time zones dynamically
    if (Intl.supportedValuesOf) {
      const zones = Intl.supportedValuesOf("timeZone");
      setTimeZones(zones);
    }
  }, []);

  useEffect(() => {
    // Update time whenever selectedZone changes
    const updateTime = () => {
      const time = new Intl.DateTimeFormat("en-US", {
        hour: "2-digit",
        minute: "2-digit",
        hour12: true,
        timeZone: selectedZone,
      }).format(new Date());
      setCurrentTime(time);
    };

    updateTime(); // initial
    const interval = setInterval(updateTime, 60000); // update every minute

    return () => clearInterval(interval);
  }, [selectedZone]);
  const handleTimeChange = (time) => {
    const zonedTime = toZonedTime(time, selectedZone); // Convert to the selected time
    const timeOnly = format(time, "hh:mm a");
    setSelectedTimehours(timeOnly);
    setSelectedTime(zonedTime);
  };
  const now = new Date();
  now.setHours(0, 0, 0, 0); // Set time to midnight for date-only comparison

  const dayPropGetter = (date) => {
    const day = new Date(date);
    day.setHours(0, 0, 0, 0);

    if (day < now) {
      return {
        style: {
          backgroundColor: "#e0e0e0", // Greyed out
          pointerEvents: "none", // Completely disables clicking/selecting
          opacity: 0.6,
          cursor: "none",
        },
      };
    }

    return {};
  };
  const handledescription = (e) => {
    setDescriptionvalue(e.target.value);
  };
  const handleSubmitPay = async (e) => {
    e.preventDefault();
    // handle form submission
    console.log(e.target);
    var vl = e.target;
    let formData = {
      name: vl.name.value,
      email: vl.email.value,
      cardnumber: vl.cardnumber.value,
      expiry: vl.expiry.value,
      cvv: vl.cvv.value,
      user_id: userLogin.id,
      plan_id: id,
    };
    try {
      const res = await axios.post(apiURL + "usersubscription", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
    setShowPopup(false);
  };
  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <MainHeader />

          <SectionWrapper className="d-block py-5">
            <div className="container-lg">
              <div className="row justify-content-center">
                <div className="col-md-3">
                  <ModuleSideNav />
                </div>
                <div className="col-md-9">
                  <form action="">
                    <Stepblock id="step5">
                      <div className="d-flex flex-column gap-5">
                        {errorMsg && (
                          <Alertpopup
                            message={errorMsg}
                            onClose={() => seterrorMsg("")}
                          />
                        )}
                        {successMsg && (
                          <Alertpopupsuccess
                            message={successMsg}
                            onClose={() => setsuccessMsg("")}
                          />
                        )}
                        <Titletext>{moduledata.name}</Titletext>
                        <div className="row gy-3">
                          <div className="col-md-6">
                            <Dealcontent>
                              <div className="d-flex flex-column gap-3">
                                <div className="klogo">
                                  <div className="inlogo fulw">
                                    <img
                                      src="/logos/logoblack.png"
                                      alt="logo"
                                    />
                                  </div>
                                </div>
                                <h3>Keiretsu Forum Conoda</h3>
                                <h4>Deal Screening - 30 minutes</h4>
                              </div>
                              <h6>
                                <StyledIcon>
                                  <Clock />
                                </StyledIcon>
                                30 min
                              </h6>
                              <h6>
                                <StyledIcon>
                                  <Video />
                                </StyledIcon>
                                Web conferencing details provided upon
                                confirmation.
                              </h6>
                              <p>
                                Keiretsu Forum has grown to be the worlds
                                largest and most active early stage investor
                                network (as ranked by Pitchbook!) with over
                                3.000 accredited investor members throughout
                                chapters in countrjes on 4 continents
                              </p>
                              <p>
                                The first step to participation es a 30 menute
                                Deat Screening Session (via on online Zoom
                                meeting). This is a no-obligation / no-cost
                                opportunity for out Deol Screening Committee
                              </p>
                            </Dealcontent>
                          </div>
                          <div className="col-md-6">
                            <div className="d-flex flex-column gap-2">
                              <label htmlFor="">Select a date and time</label>
                              <Iconblock>
                                <Calendar
                                  localizer={localizer}
                                  events={selectedSlots}
                                  startAccessor="start"
                                  endAccessor="end"
                                  selectable={true}
                                  onSelectSlot={handleSelectSlot}
                                  onSelectEvent={(event) =>
                                    console.log("Clicked event", event)
                                  }
                                  views={["month"]}
                                  style={{ height: 300 }}
                                  popup
                                  dayPropGetter={dayPropGetter}
                                />

                                <strong>Time Zone</strong>
                                <div>
                                  <select
                                    value={selectedZone}
                                    onChange={(e) =>
                                      setSelectedZone(e.target.value)
                                    }
                                  >
                                    {timeZones.map((tz) => (
                                      <option key={tz} value={tz}>
                                        {tz} (
                                        {new Intl.DateTimeFormat("en-US", {
                                          hour: "2-digit",
                                          minute: "2-digit",
                                          hour12: true,
                                          timeZone: tz,
                                        }).format(new Date())}
                                        )
                                      </option>
                                    ))}
                                  </select>
                                </div>
                              </Iconblock>
                              <button
                                style={{
                                  opacity: disabledButton ? 0.5 : 1,
                                  pointerEvents: disabledButton
                                    ? "none"
                                    : "auto",
                                }}
                                className="registerzoom"
                                onClick={() => setShowPopup(true)}
                                type="button"
                              >
                                Register For Zoom
                              </button>
                            </div>
                          </div>
                          <div className="col-12">
                            <Iconblock>
                              <Building />
                              <textarea
                                id=""
                                rows="5"
                                onInput={handledescription}
                                name="description"
                                placeholder="The marketing description will go here. It will outline the 'Be The Investor' module."
                              ></textarea>
                            </Iconblock>
                          </div>
                        </div>
                      </div>
                    </Stepblock>
                  </form>
                </div>
              </div>
            </div>
          </SectionWrapper>
        </div>

        <Overlay show={showPopup}>
          <PopupBox>
            <CloseBtn onClick={() => setShowPopup(false)}>×</CloseBtn>
            <form onSubmit={handleSubmit}>
              <div className="d-flex flex-column gap-3">
                <h3 className="text-center">Register</h3>
                <div className="d-block">
                  <Input
                    type="text"
                    name="name"
                    placeholder="Your Name *"
                    value={formData.name}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="d-block">
                  <Input
                    type="email"
                    name="email"
                    placeholder="Your Email *"
                    value={formData.email}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="d-block">
                  <DatePicker
                    name="time"
                    selected={selectedTime}
                    onChange={handleTimeChange}
                    showTimeSelect
                    showTimeSelectOnly
                    timeIntervals={15}
                    dateFormat="h:mm aa"
                    placeholderText="Select Time *"
                  />
                </div>
                <div className="d-flex align-items-center justify-content-end gap-2">
                  {loader && (
                    <div
                      className="spinner-border text-dark"
                      role="status"
                      style={{ width: "1.5rem", height: "1.5rem" }}
                    >
                      <span className="visually-hidden">Loading...</span>
                    </div>
                  )}
                  <SubmitBtn
                    type="submit"
                    style={{
                      width: "100%",
                      opacity: loader ? 0.5 : 1,
                      pointerEvents: loader ? "none" : "auto",
                    }}
                  >
                    Submit For Zoom
                  </SubmitBtn>
                </div>
              </div>
            </form>
          </PopupBox>
        </Overlay>
      </Wrapper>
      <PaymentPopup
        moduledata={moduledata}
        show={showPopupPay}
        onClose={() => setShowPopupPay(false)}
        onSubmit={handleSubmitPay}
      />
    </>
  );
}
